.loader {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    width: 3em;
    height: 3em;
    margin: -2.5em 0 0 -1.5em;
    border: 16px solid black;
    border-radius: 50%;
    border-top: 16px solid rgba(0, 0, 0, .5);
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

